import { GetComponentProps, Modal, useEditor, useEditorActions } from 'topwrite';
import { ReactComponent as Icon } from '../images/icon.svg';
import queryString from 'query-string';

export const PATTERN = /^https:\/\/www\.bilibili\.com\/video\/(.*)/;

export default function Tool({ Component }: GetComponentProps<'editor:tool'>) {
    const editor = useEditor();

    const handler = async () => {
        const { insertXBlock } = useEditorActions();

        const result = await Modal.prompt<string>({
            title: '插入哔哩哔哩视频',
            schema: {
                type: 'string',
                title: '视频地址',
            },
            validate(url, errors) {
                if (url && !url.match(PATTERN)) {
                    errors.addError('视频地址有误，请检查');
                }
                return errors;
            }
        });

        if (result) {
            const value = queryString.parseUrl(result).url;
            insertXBlock(editor, {
                name: 'bilibili',
                value
            });
        }
    };

    return <Component
        title='哔哩哔哩视频'
        handler={handler}
    >
        <Icon />
    </Component>;
}
