import { GetComponentProps, styled } from 'topwrite';
import { PATTERN } from '@/components/tool';

export default function Bilibili({ props: { value } }: GetComponentProps<'x-block'>) {

    const match = PATTERN.exec(value);
    if (match) {
        return <StyledIframe src={`//player.bilibili.com/player.html?bvid=${match[1]}`} allowFullScreen />;
    }

    return null;

}

const StyledIframe = styled.iframe`
  width: 100%;
  height: 500px;
  border: 1px solid #EFF0F0;
  border-radius: 4px;
`;
